@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");


html{
  min-height: 100%;
}

.dark {
  background: #a1a1a1;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Lato";
  background: url(/img/mario-bg.png) no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color: #ffffff;
  color: rgb(20, 20, 20);
  min-height: 100%;

}

